body, html{
	font-family: arial,sans-serif;
	display: flex;
	flex-direction: column;
	height: 100%;
	
	//background-color: @color_background_main;
}
html{
	max-width: 800px;
	margin: 0 auto;
	margin-top: 0px;
}
header{
	max-width: 800px;
	width: 100%;
	background-color: #fff;
	position: fixed;
	z-index: 1000;
	.wrapper{
	height: 40px;
	padding-bottom: 10px;
	line-height: 50px; //Выравнивение текста по вертикали
	text-overflow: ellipsis; //образка длинных блоков с текстом
	overflow: hidden; //прятать выходящий за пределы текст
	white-space: nowrap; //запрет на перенос текста

	
	.arrow{
		display: inline-block;
		font-size: 25px; 
		padding-left: 5px;
	}
	.arrow:hover{
	  //background: #F2F2F2;
	  color: orange;
	}
	.arrow:active{
	  //background: #F2F2F2;
	  color: green;
	}
	a{	
		text-decoration: none;
		color: #FF0000; 
	}
	h1{
		display: inline-block;
		font-size: 20px;
		padding-left: 10px;
	   	}
	}
}
/*   About   */
.mainPage{
		width: calc(100% - 15px);
	    display: flex;
	    flex-wrap: nowrap;
	    justify-content: space-between;
		h1{
		display: inline-block;
		font-size: 20px;
		padding-left: 10px;
	   	}
	   	.about{
		    border-radius: 100%;
		    width: 20px;
		    height: 20px;
		    line-height: 20px;
		    font-size: 18px;
		    align-self: center;
		    color: rgb(68,68,68);
	  		text-decoration: none;
	  		user-select: none;
	  		outline: none;
	  		border: 1px solid rgba(0,0,0,.1);
	  background: rgb(245,245,245) linear-gradient(#f4f4f4, #f1f1f1);
	  	transition: all .218s ease 0s;
	   	}
	   	.about:hover {
		  color: rgb(24,24,24);
		  border: 1px solid rgb(198,198,198);
		  background: #f7f7f7 linear-gradient(#f7f7f7, #f1f1f1);
		  box-shadow: 0 1px 2px rgba(0,0,0,.1);
		}
		.about:active {
		  color: rgb(51,51,51);
		  border: 1px solid rgb(204,204,204);
		  background: rgb(238,238,238) linear-gradient(rgb(238,238,238), rgb(224,224,224));
		  box-shadow: 0 1px 2px rgba(0,0,0,.1) inset;
		}
}		
.main{
	flex-grow: 1;
	flex-shrink: 0;
	margin-top: 50px;
	 .wrapper{
	 	min-height: 450px;
	 	height: 100%;
	 	.show{
	 		&__streamline img{
 			   	//width: calc(100% - 14px);
 			   	width: 30%;
			    margin-left: 5px;
			    border: 1px solid rgba(0,0,0,.1);
			    border-radius: 2px;
	 		}
	 	}
	 }

	.base_title{
		.row-flex();
		.col();
		flex-direction: column;
		align-items: center;
	
		a{
			.row-flex();
			.col();
			align-items: center;
			width: calc(100% - 10px);
			height: 50px;
			font-size: 20px;
			text-transform: uppercase;
			margin-bottom: 20px;
			//background: @color_background_menu;
			div{	
				width: 100%;
				text-decoration: none;
				color: #333;
				margin-left: 5px;
				margin-right: 5px;
			}
		}



	}


}


