@font-face {
  font-family: 'olietta_script_poesia';
  src: url("../fonts/olietta_script_poesia.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

/* table irregular Verbs Start */

.table {
  width: calc(100% - 10px);
  margin-left: 5px;
  font-size: 22px;
  margin: 0 auto;
  text-align: left;
  background-color: #E6E6E6;
}

.table__row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  line-height: 1.5em;
}

.table__row span {
  padding-top: 2px;
  padding-bottom: 2px;
  padding-left: 3px;
  padding-right: 3px;
}

.table__row:nth-child(2n+1) {
  background: #f7f7f7;
}

.voice {
  cursor: pointer;
}

/* table irregular Verbs End */

.tableTest__row {
  display: flex;
  flex-wrap: wrap;
  font-size: 22px;
  margin-bottom: 15px;
}

.tableTestMobile .tableTest__slide {
  display: none;
  padding-left: 5px;
}

.tableTestMobile .tableTest__slide:first-child {
  display: block;
}

/* Input Ansver */

.row {
  position: relative;
  font-size: 24px;
  width: calc(100% - 8px);
  border-radius: 2px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(0, 0, 0, 0.08);
  height: 44px;
}

.row input {
  border: none;
  width: calc(100% - 14px);
}

.row:hover {
  box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.2), 0 0 0 1px rgba(0, 0, 0, 0.08);
}

label {
  color: #999;
  transition: all 0.1s ease-in-out;
}

[type=text],
label {
  position: absolute;
  left: 0;
  top: 0;
  padding: 10px 2px 4px 10px;
}

[type=text] {
  outline: 0;
}

[type=text]::placeholder {
  color: transparent;
}

[type=text]:not(:placeholder-shown) + label {
  font-size: 9px;
  margin: -6px 0 0 1px;
}

/* End */

.button {
  width: calc(100% - 13px);
  padding-left: 5px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: flex-end;
}

.button #btnBack,
.button #btnNext,
.button #testResult,
.button #restartTest,
.button #hint,
.button .psevBtnBack {
  flex-basis: 25%;
  margin-top: 5px;
  margin-bottom: 0;
}

.button #btnBack {
  display: none;
}

.buttonResult .show__next {
  width: auto;
  margin: 5px auto;
}

.numbQuestion {
  margin-top: 5px;
  text-align: center;
}

#testResult,
#restartTest {
  display: none;
}

.tableTest__result {
  flex-wrap: wrap;
  font-size: 22px;
  margin-bottom: 5px;
}

.tableTest__result__right {
  color: green;
}

.tableTest__result__badAnsver {
  color: red;
}

.tableTest__result__quest {
  color: red;
}

.psevBtnBack {
  text-align: center;
  display: inline-block;
  font-size: 20px;
  text-transform: uppercase;
  color: #444;
  padding: 10px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  box-sizing: border-box;
  user-select: none;
}

.show {
  width: calc(100% - 35px);
  margin-left: 5px;
}

.show a {
  text-decoration: none;
}

.show__next {
  width: 100%;
  display: inline-block;
  font-size: 20px;
  text-transform: uppercase;
  color: #444444;
  text-decoration: none;
  user-select: none;
  padding: 10px 10px;
  margin-bottom: 20px;
  outline: none;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  background: #f5f5f5 linear-gradient(#f4f4f4, #f1f1f1);
  transition: all 0.218s ease 0s;
}

.show__next:hover {
  color: #181818;
  border: 1px solid #c6c6c6;
  background: #f7f7f7 linear-gradient(#f7f7f7, #f1f1f1);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}

.show__next:active {
  color: #333333;
  border: 1px solid #cccccc;
  background: #eeeeee linear-gradient(#eeeeee, #e0e0e0);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1) inset;
}

