 /* table irregular Verbs Start */
.table{
	width: calc(100% - 10px);
	margin-left: 5px;
	font-size: 22px;
	margin: 0 auto;
    text-align: left;
    background-color: #E6E6E6;
    @media screen and (max-width: 1230px){
	    	font-size: 20px;
	    }
       @media screen and (max-width: 1145px){
    	font-size: 18px;
    }
      @media screen and (max-width: 1095px){
    	font-size: 18px;
    }
}
.table__row{
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	line-height: 1.5em;
}
.table__row span{
	padding-top: 2px;
	padding-bottom:2px;
	padding-left: 3px;
	padding-right: 3px;
}
.table__row:nth-child(2n+1){
	background: #f7f7f7;
}
.voice{
	cursor: pointer;
}
/* table irregular Verbs End */