.tableTest__row{
	display: flex;
	flex-wrap: wrap;
	font-size: 22px;
	margin-bottom: 15px;
}
.tableTestMobile .tableTest__slide{
	display: none;
	padding-left: 5px;
}

.tableTestMobile .tableTest__slide:first-child{
	display: block;
}
/* Input Ansver */
.row{
    position: relative;
    font-size: 24px;
	width: calc(100% - 8px);
    border-radius: 2px;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.16), 0 0 0 1px rgba(0,0,0,0.08);
    height: 44px;
    input{
    	border: none;
    	width: calc(100% - 14px);
    }
}
.row:hover{
	box-shadow:0 3px 8px 0 rgba(0,0,0,0.2),0 0 0 1px rgba(0,0,0,0.08);
}

label{
   color: #999;
   transition: all .1s ease-in-out;
}
[type=text],label{
   position:absolute;
   left: 0; top: 0;
   padding: 10px 2px 4px 10px
}
[type=text]{
   outline: 0;
}
[type=text]::placeholder,{
   color: transparent
}
[type=text]:not(:placeholder-shown) + label,{
   font-size: 9px;
   margin: -6px 0 0 1px;
}
/* End */
.button{
	width: calc(100% - 13px);
	padding-left: 5px;
	display: flex;
	flex-wrap: nowrap;
	justify-content: space-between;
	align-items: flex-end;
	#btnBack, #btnNext, #testResult, #restartTest, #hint, .psevBtnBack{
		flex-basis: 25%;
		margin-top: 5px;
		margin-bottom: 0;
	}
	#btnBack{
		display: none;
	}

}
.buttonResult{
	.show__next{
		width: auto;
		margin: 5px auto;
	}
}
.numbQuestion{
		margin-top: 5px;
		text-align: center;
	}
#testResult, #restartTest{
	display: none;
}
.tableTest__result{
	//display: flex;
	flex-wrap: wrap;
	font-size: 22px;
	margin-bottom: 5px;
		&__right{
		color: green;
		}
		&__badAnsver{
			color: red;
		}
		&__quest{
			color: red;
		}
		
}
.psevBtnBack{
	text-align: center;
    display: inline-block;
    font-size: 20px;
    text-transform: uppercase;
    color: #444;
    padding: 10px;
    border: 1px solid rgba(0,0,0,.1);
    border-radius: 2px;
    box-sizing: border-box;
    user-select: none;  //запрещаю выделение текста
}